.hader-container {
  position: relative;
  width: 100%;
}
.video-container {
  height: 100vh;
}

.contant-container {
  position: absolute;
  width: 100%;
  height: 100%;

  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
}

.logo {
  background-image: url(../../../../assets/pictures/logo/first.png);
  height: 70%;
  width: 750px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  transform: scale(1);
  transition: all 0.6s ease-in-out;
  z-index: 3;
}

.contant {
  color: white;
  font-size: 24px;
}

@media screen and (max-width: 1110px) {
  .contant-container {
    flex-direction: row;
  }
  .video-container {
    height: 340px;
  }
  .logo {
    height: 90%;
    width: 500px;
  }
  .contant {
    font-size: 10px;
  }
}
